.dialogs_item {
    border-right: 1px solid #edeef0;
    border-bottom: 1px solid #edeef0;
    border-left: 1px solid #edeef0;

    padding: 10px 15px;

    cursor: pointer;
    display: flex;

    transition: .2s linear;
}

.dialogs_item:hover {
    background-color: #f1f1f1;
}

.friends_name {
    padding-left: 20px;
}

.friends_name div{
    display: flex;
    padding-top: 10px;
}

.friends_name p {
    margin: auto;
}

.friends_ph img {
    width: 50px;
    border-radius: 30px;
}

.img_name-i {
    width: 30px;
    border-radius: 30px;

    margin-right: 10px;
}

.friends_name_text {
    font-size: 14px;
}

.dialogs_text {
    font-size: 12px;
    font-weight: 300;
    color: #666666;
}